const state = {
  extension1: {},
  extension2: {},
  extension3: {},
  extension4: {},
  extension5: {}
};

const mutations = {
  setExtension1(state, value) {
    state.extension1 = value;
  },
  setExtension2(state, value) {
    state.extension2 = value;
  },
  setExtension3(state, value) {
    state.extension3 = value;
  },
  setExtension4(state, value) {
    state.extension4 = value;
  },
  setExtension5(state, value) {
    state.extension5 = value;
  }
};

export default {
  namespaced: true,
  state,
  mutations
};
