<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />
  </div>
</template>
<script>
import { DecryptData } from "@/utils/crypto.js";
export default {
  name: "App",
  provide() {
    //父组件中通过provide来提供变量，在子组件中通过inject来注入变量。
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isRouterAlive: true, //控制视图是否显示的变量
    };
  },
  methods: {
    reload() {
      this.isRouterAlive = false; //先关闭，
      this.$nextTick(function() {
        this.isRouterAlive = true; //再打开
      });
    },
  },
  created() {
    if (window.location.href.search("DistributionUofId") != -1) {
      var a = window.location.href.split("?")[1];
      var b1 = a.split("&")[0];
      var b2 = a.split("&")[1];
      var c1 = b1.split("DistributionUofId=")[1];
      var c2 = b2.split("date=")[1];
      // 把空格替换成+号
      c1 = c1.replace(/%20/g, "%2B");
      c2 = c2.replace(/%20/g, "%2B");
      // urf-8转换
      var DistributionUofId = DecryptData(decodeURIComponent(c1));
      var data = DecryptData(decodeURIComponent(c2));
      // 获取当前时间戳
      let newData = new Date().getTime();
      let oldData = this.dataChange(data);

      if (newData - oldData > 10800000) {
        // console.log("现在超过三小时了");
        sessionStorage.setItem("dataTime", true);
      } else {
        sessionStorage.setItem("dataTime", false);
      }
      // 存入本地缓存
      sessionStorage.setItem("DistributionUofId", DistributionUofId);
      sessionStorage.setItem("data", data);
    } else {
      if (sessionStorage.getItem("DistributionUofId")) {
        return false;
      }
      console.log(sessionStorage.getItem("DistributionUofId"));
      sessionStorage.setItem("DistributionUofId", "watervip");
    }
  },
  mounted() {
    window.onbeforeunload = function() {
      var storage = window.localStorage;
      storage.clear();
    };
  },
  methods: {
    dataChange(time) {
      let Y = time.substring(0, 4);
      let M = time.substring(4, 6);
      let D = time.substring(6, 8);
      let h = time.substring(8, 10);
      let m = time.substring(10, 12);
      let s = time.substring(12, 14);
      let datas = Y + "-" + M + "-" + D + " " + h + ":" + m + ":" + s;
      let oldData = new Date(datas).getTime();
      return oldData;

      // let oldData = new Date(datas).getTime();
      // let dataTime = 10800000;
      // console.log(newData);
      // console.log(oldData);
      // console.log(dataTime);
      // console.log(newData - oldData);
    },
  },
};
</script>

