const state = {
  replace1: {},
  replace2: {},
  replace3: {},
  replace4: {},
  replace5: {}
};

const mutations = {
  setReplace1(state, value) {
    state.replace1 = value;
  },
  setReplace2(state, value) {
    state.replace2 = value;
  },
  setReplace3(state, value) {
    state.replace3 = value;
  },
  setReplace4(state, value) {
    state.replace4 = value;
  },
  setReplace5(state, value) {
    state.replace5 = value;
  },
};

export default {
  namespaced: true,
  state,
  mutations
}