import CryptoJS from "crypto-js/crypto-js";

// 默认的 KEY 与 iv
const KEY = CryptoJS.enc.Utf8.parse("F285FC35F252B");
const IV = CryptoJS.enc.Utf8.parse("73EB2888622B6");
/**
 * AES加密 ：字符串 key iv  返回base64
 */
export function Encrypt(word, keyStr, ivStr) {
  let key = KEY;
  let iv = IV;

  if (keyStr) {
    key = CryptoJS.enc.Utf8.parse(keyStr);
    iv = CryptoJS.enc.Utf8.parse(ivStr);
  }

  let srcs = CryptoJS.enc.Utf8.parse(word);
  var encrypted = CryptoJS.AES.encrypt(srcs, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.ZeroPadding,
  });
  // console.log("-=-=-=-", encrypted.ciphertext)
  return CryptoJS.enc.Base64.stringify(encrypted.ciphertext);
}
/**
 * AES 解密 ：字符串 key iv  返回base64
 *
 */
export function Decrypt(word, keyStr, ivStr) {
  let key = KEY;
  let iv = IV;

  if (keyStr) {
    key = CryptoJS.enc.Utf8.parse(keyStr);
    iv = CryptoJS.enc.Utf8.parse(ivStr);
  }

  let base64 = CryptoJS.enc.Base64.parse(word);
  let src = CryptoJS.enc.Base64.stringify(base64);

  var decrypt = CryptoJS.AES.decrypt(src, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.ZeroPadding,
  });

  var decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
  return decryptedStr.toString();
}


// 解密封装，md5
export function DecryptData(encryptedData) {
  try {
    let md5Key = CryptoJS.MD5('F285FC35F252B');
    let md5Iv = CryptoJS.MD5('73EB2888622B6');
    let ciphertext = CryptoJS.enc.Base64.parse(encryptedData);
    let plaintext = CryptoJS.AES.decrypt(encryptedData, md5Key, {
      iv: md5Iv,
      mode: CryptoJS.mode.CBC
    });
    return CryptoJS.enc.Utf8.stringify(plaintext);
  } catch (err) {
    return err;
  }
}