import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "@/assets/css/reset.scss";
import "vant/lib/index.css";
// eslint-disable-next-line no-unused-vars
import axios from "axios";
import { post, fetch, patch, put } from "./utils/request";
// import { DistributionUofId } from "./utils/DistributionUofId";
import {
  Popup,
  RadioGroup,
  Radio,
  Field,
  Uploader,
  ActionSheet,
  Toast,
  Checkbox,
  CheckboxGroup,
  Notify,
  Icon
} from "vant";
import api from "./utils/fileApi.js"; //这里注意自己的路径
Vue.prototype.$api = api; //注册为全局变量
Vue.use(Popup);
Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Field);
Vue.use(Uploader);
Vue.use(ActionSheet);
Vue.use(Toast);
Vue.use(Checkbox);
Vue.use(CheckboxGroup);
Vue.use(Notify);
Vue.use(Icon);
//定义全局变量
Vue.prototype.$post = post;
Vue.prototype.$fetch = fetch;
Vue.prototype.$patch = patch;
Vue.prototype.$put = put;
Vue.prototype.$DistributionUofId = sessionStorage.getItem("DistributionUofId");
Vue.prototype.$axios = axios;

Vue.config.productionTip = false;

// 浏览器title赋值
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  } else {
    document.title = "夥伴雲";
  }
  next();
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
