<template>
  <div class="header">
    <div class="headerImg">
      <img
        @click="toInstallReplacement()"
        draggable="false"
        class="headerLogo"
        src="@/assets/image/headerLogo.png"
        alt=""
      />
      <img
        @click="show = !show"
        draggable="false"
        class="headerMenu"
        src="@/assets/image/headerMenu.png"
        alt=""
      />
    </div>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
    <div class="BackButton" @click="$router.go(-1)">返回</div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      show: false,
      actions: [
        { name: "客戶資料" },
        { name: "預約服務" },
        { name: "商機通報" },
        // { name: "商品兌換" },
        { name: "濾心更換" },
        { name: "淨水器安裝" },
        { name: "水質查詢" },
        // { name: "電商轉單" },
      ]
    };
  },
  methods: {
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      if (item.name == "客戶資料") {
        if (this.$route.path == "/CustomerService/CustomerData") {
          return false;
        } else {
          this.$router.push("/CustomerService/CustomerData");
        }
      } else if (item.name == "預約服務") {
        if (this.$route.path == "/CustomerService") {
          return false;
        } else {
          this.$router.push("/CustomerService");
        }
      } else if (item.name == "商機通報") {
        if (this.$route.path == "/CustomerService/CommercialOpportunity") {
          return false;
        } else {
          this.$router.push("/CustomerService/CommercialOpportunity");
        }
      } else if (item.name == "商品兌換") {
        if (this.$route.path == "/CustomerService/CommodityExchange") {
          return false;
        } else {
          this.$router.push("/CustomerService/CommodityExchange");
        }
      } else if (item.name == "濾心更換") {
        if (this.$route.path == "/InstallReplacement") {
          return false;
        } else {
          this.$router.push("/InstallReplacement");
        }
      } else if (item.name == "淨水器安裝") {
        if (this.$route.path == "/InstallReplacement/Replacement") {
          return false;
        } else {
          this.$router.push("/InstallReplacement/Replacement");
        }
      } else if (item.name == "水質查詢") {
        if (this.$route.path == "/WaterQuality") {
          return false;
        } else {
          this.$router.push("/WaterQuality");
        }
      } else if (item.name == "電商轉單") {
        if (this.$route.path == "/InstallReplacement/Transfer") {
          return false;
        } else {
          this.$router.push("/InstallReplacement/Transfer");
        }
      }
    },
    toInstallReplacement() {
      if (this.$route.path == "/InstallReplacement") {
        return false;
      } else {
        this.$router.push("/InstallReplacement");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  height: 50px;
  width: 100%;
  .headerImg {
    background: #ffffff;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3;
    width: 89.34%;
    padding: 0 5.33%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .headerLogo {
      width: 119px;
      height: 12px;
    }
    .headerMenu {
      height: 16px;
      width: 21px;
    }
  }
  .BackButton {
    cursor: pointer;
    z-index: 999;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: fixed;
    right: 20px;
    bottom: 20px;
    text-align: center;
    line-height: 50px;
    color: #ffffff;
    font-size: 14px;
    background: linear-gradient(180deg, #1c76fe 0%, #1c76fe 100%);
  }
}
</style>
