<template>
  <div class="HeaderTitle">
    <div class="headerContent">
      <p>{{ title }}</p>
      <em>經銷商編號：{{ number }}</em>
    </div>
  </div>
</template>

<script>
import { POST_GetDistributionByUofId } from "@/utils/api";
export default {
  name: "Header",
  data() {
    return {
      number: "",
      title: "",
    };
  },
  created() {
    this.GetDistributionByUofId();
  },
  methods: {
    // 获取到府服务列表
    GetDistributionByUofId() {
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
      };
      POST_GetDistributionByUofId(UofIds).then((response) => {
        this.title = response.Data.Data.Name;
        this.number = response.Data.Data.Id;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.HeaderTitle {
  width: 100%;
  background: #ffffff;
  .headerContent {
    padding-left: 7.47%;
    height: 60px;
    p {
      font-size: 20px;
      color: #3d3d3d;
      line-height: 60px;
      display: inline-block;
    }
    em {
      font-size: 12px;
      color: #868686;
      line-height: 50px;
      margin-left: 2.67%;
    }
  }
}
</style>
