/* eslint-disable no-unused-vars */
import axios from "axios";
import qs from "qs";

axios.defaults.timeout = 300000;
axios.defaults.baseURL = "https://crmreport.sakura.com.tw";

//http request 攔截器
axios.interceptors.request.use(
  (config) => {
    // eslint-disable-next-line no-self-assign
    config.data = config.data;
    if (
      config.url == "API/CreateWaterInstallImage" ||
      config.url == "API/GetMFNO"
    ) {
      config.headers = {
        "Content-Type": "multipart/form-data",
      };
    } else {
      config.headers = {
        "Content-Type": "application/x-www-form-urlencoded; charset=utf-8",
      };
    }
    // if(token){
    //   config.params = {'token':token}
    // }
    return config;
  },
  (error) => {
    // eslint-disable-next-line no-undef
    return Promise.reject(err);
  }
);

//http response 拦截器
axios.interceptors.response.use(
  (response) => {
    if (sessionStorage.getItem("dataTime")) {
      // console.log("超时了，拦截请求并跳转页面");
    }
    // if (response.data.errCode == 2) {
    //   // eslint-disable-next-line no-undef
    //   router.push({
    //     path: "/login",
    //     // eslint-disable-next-line no-undef
    //     query: { redirect: router.currentRoute.fullPath } //从哪个页面跳转
    //   });
    // }
    return response;
  },
  (error) => {
    return Promise.reject(error);
  }
);

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
      })
      .then((response) => {
        resolve(response.data);
      })
      .catch((err) => {
        reject(err);
      });
  });
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, qs.stringify(data)).then(
      (response) => {
        resolve(response.data);
      },
      (err) => {
        reject(err);
      }
    );
  });
}

